.ChartContainer {
    max-width: 100%;
    margin: 40px auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.PieChart {
    width: 100%;
    height: 100%;
}

.Pie {
    stroke: #fff;
    stroke-width: 2;
}

.Cell {
    transition: all 0.3s ease;
}

.Cell:hover {
    transform: scale(1.1);
}

.Tooltip {
    background-color: #333;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    font-size: 14px;
}

.Legend {
    font-size: 14px;
    margin-top: 20px;
    text-align: center;
}

.Legend li {
    display: inline-block;
    margin-right: 20px;
}

.Legend li span {
    display: inline-block;
    width: 10px;
    height: 10px;
    margin-right: 5px;
    border-radius: 50%;
}

/* Phone responsive styles */
@media only screen and (max-width: 600px) {
    .ChartContainer {
        margin: 20px auto;
        padding: 10px;
    }

    .PieChart {
        width: 300px;
        height: 300px;
    }

    .Legend {
        font-size: 12px;
    }

    .Legend li {
        margin-right: 10px;
    }
}

@media only screen and (max-width: 400px) {
    .ChartContainer {
        margin: 10px auto;
        padding: 5px;
    }

    .PieChart {
        width: 200px;
        height: 200px;
    }

    .Legend {
        font-size: 10px;
    }

    .Legend li {
        margin-right: 5px;
    }
}