/* LApp.css */

.containerproduct {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.containerproduct li {
    margin: 20px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: calc(25% - 40px);
}

.containerproduct li img {
    width: 160px;
    height: 100px;
    
    border-radius: 10px 10px 0 0;
}

.containerproduct li span {
    display: block;
    margin-bottom: 10px;
}

/* Phone responsive styles */
@media only screen and (max-width: 600px) {
    .containerproduct li {
        width: calc(50% - 40px);
    }
}

@media only screen and (max-width: 400px) {
    .containerproduct li {
        width: 100%;
    }
}